.sliderBox {
   display: -ms-flexbox;
   display: flex;
   -ms-flex-align: center;
       align-items: center;
   width: 100%;
}

.sliderBox div {
    height: 100px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    padding: 0 40px;
    border-top: solid 1px #363636;
    border-bottom: solid 1px #363636;
    margin-top: 80px;
}
.sliderBox div:hover {
    color:white;
    background: #2050C2;
}

.sliderBox div:first-child, .sliderBox div:last-child {
    border-left: solid 1px #363636;
    border-right: solid 1px #363636;
}
hr {
    margin: 80px auto;
    color: #f6f6f6 !important;
    border: 1px solid #f6f6f6 !important;
}
.valveTiles img {
    width: 100%;
}
.thirdvalve p:last-child {
    margin-bottom: 80px;
}
.video { 
    margin-top: -6px;
}
@media screen and (max-width: 1160px) {
    .moreFeatues {
        max-width: 100%;
    }
    .moreFeatues .row .col.l4 {
        width: 100% !important;
    }
    .moreFeatues.two {
        padding-top: 0px !important;
    }
}
@media screen and (max-width: 951px) {
    .sliderBox div {
        padding: 0 20px; 
        height: 80px;
    }

}
@media screen and (max-width: 600px) {
    .sliderBox {
        -ms-flex-pack: center;
            justify-content: center;
    }
    .controllerFeatures h2{
        padding: 0px 0;
    }
    .controllerFeatures {
        margin: 80px auto 0px auto;
    }
    .controllerFeatures img {
        display: block;
        margin: auto;
    }
    .app {
        top: 0px !important;
        width: 80% !important;
    
    }
    .thirdvalve h2, .thirdvalve p {
        font-size: 14px;
        width: 95%;
        max-width: 95%;
        margin: 20px auto;
    }
    .thirdvalve h2:first-child {
        margin-top: 40px;
    }
    hr {
        margin: 40px auto;
    }

    
}


